.exit-done {
  -webkit-animation-name: exitDone;
          animation-name: exitDone;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

@-webkit-keyframes exitDone {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes exitDone {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.exit-up {
  -webkit-animation-name: exitUp;
          animation-name: exitUp;
  -webkit-animation-duration: .3s;
