/* Reset this shit */
h1, h4 {
  margin: 0;
}

h1 {
  font-family: 'Lato', sans-serif;
  font-size: calc(4vw + 2.5vh);
  margin-bottom: 4px;
}

h4 {
  font-family: 'Roboto Condensed', sans-serif;
